@import url(https://fonts.googleapis.com/css2?family=Istok+Web&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gudea&family=Trocchi&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Istok Web", sans-serif;
  background-color: #f8f1e9;
}

h1,
h2,
h3 {
  font-family: "Trocchi", serif;
}

nav .nav-wrapper {
  position: relative;
  background-color: #a14016;
}

.element-position {
  position: absolute;
  top: 8px;
  right: 15px;
}

.element-position:hover :nth-last-child(4) {
  -webkit-animation: star 1s infinite ease-in;
          animation: star 1s infinite ease-in;
  -webkit-animation-delay: 0.33s;
          animation-delay: 0.33s;
}
.element-position:hover :nth-last-child(3) {
  -webkit-animation: star 1s infinite ease-in;
          animation: star 1s infinite ease-in;
  -webkit-animation-delay: 0.66s;
          animation-delay: 0.66s;
}
.element-position:hover :nth-last-child(2) {
  -webkit-animation: star 1s infinite ease-in;
          animation: star 1s infinite ease-in;
  -webkit-animation-delay: 0.99s;
          animation-delay: 0.99s;
}

@-webkit-keyframes star {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes star {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

ul.left {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.btn-large {
  background-color: #322938;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 200px;
}

.btn-large:hover {
  background-color: #89a194;
  color: #444;
}

svg {
  cursor: pointer;
}

.loading {
  -webkit-animation: rotate 1s linear infinite;
          animation: rotate 1s linear infinite;
}

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(90deg);
  }

  50% {
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(270deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  25% {
    transform: rotate(90deg);
  }

  50% {
    transform: rotate(180deg);
  }

  75% {
    transform: rotate(270deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

span.badge.new {
  background-image: linear-gradient(
    rgb(255, 61, 0),
    rgb(179, 49, 10) 85%,
    rgb(240, 97, 53)
  );
}

span.badge.new:after {
  content: " CREDITS";
}

a.active:hover {
  background-color: transparent;
}

